"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Copy, Link2, Wallet, TrendingUp, Users, DollarSign } from "lucide-react";
import { useAffiliateDashboardContext } from "./_context/affiliate-dashboard-context";

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}

export default function AffiliateOverviewPage() {
  const {
    stats,
    links,
    canCreateMore,
    payoutMonth,
    payoutPercent,
    requestingPayout,
    monthLabel,
    setPayoutMonth,
    setPayoutPercent,
    actions,
  } = useAffiliateDashboardContext();

  const [creatingLink, setCreatingLink] = useState(false);

  const handleCreateLink = async () => {
    setCreatingLink(true);
    try {
      await actions.createLink();
    } finally {
      setCreatingLink(false);
    }
  };

  return (
    <div className="max-w-7xl mx-auto space-y-8">
      {/* Header */}
      <div className="text-center py-8">
        <h1 className="text-3xl font-bold text-gray-900">Affiliate Dashboard</h1>
        <p className="text-gray-600 mt-2">Track your referrals and earnings</p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Current Commission</CardTitle>
            <DollarSign className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(stats.currentCommission)}</div>
            <p className="text-xs text-muted-foreground">This month</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Referrals</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.referrals}</div>
            <p className="text-xs text-muted-foreground">Pharmacies onboarded</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Pending Payout</CardTitle>
            <Wallet className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(stats.pendingPayout)}</div>
            <p className="text-xs text-muted-foreground">Awaiting approval</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Paid</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(stats.paidPayout)}</div>
            <p className="text-xs text-muted-foreground">Lifetime earnings</p>
          </CardContent>
        </Card>
      </div>

      {/* Quick Actions */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Link2 className="h-5 w-5" />
              Create Referral Link
            </CardTitle>
            <CardDescription>
              Generate a new link to share with potential partners
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Button
              onClick={handleCreateLink}
              disabled={!canCreateMore || creatingLink}
              className="w-full"
            >
              {creatingLink ? "Creating..." : canCreateMore ? "Create Link" : "Max Links Reached"}
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Wallet className="h-5 w-5" />
              Request Payout
            </CardTitle>
            <CardDescription>
              Submit a request for your earned commissions
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="text-sm font-medium">Month</label>
                <Input
                  value={payoutMonth}
                  onChange={(e) => setPayoutMonth(e.target.value)}
                  placeholder="YYYY-MM"
                  className="mt-1"
                />
              </div>
              <div>
                <label className="text-sm font-medium">Percent</label>
                <Input
                  type="number"
                  min={1}
                  max={20}
                  step={0.5}
                  value={payoutPercent}
                  onChange={(e) => setPayoutPercent(Number(e.target.value))}
                  className="mt-1"
                />
              </div>
            </div>
            <Button
              onClick={actions.requestPayout}
              disabled={requestingPayout}
              className="w-full"
            >
              {requestingPayout ? "Submitting..." : "Submit Request"}
            </Button>
          </CardContent>
        </Card>
      </div>

      {/* Referral Links */}
      <Card>
        <CardHeader>
          <CardTitle>Your Referral Links</CardTitle>
          <CardDescription>
            Manage your active referral links
          </CardDescription>
        </CardHeader>
        <CardContent>
          {links.length === 0 ? (
            <div className="text-center py-8">
              <Link2 className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
              <p className="text-muted-foreground">No referral links yet</p>
              <Button onClick={handleCreateLink} className="mt-4" disabled={!canCreateMore || creatingLink}>
                Create Your First Link
              </Button>
            </div>
          ) : (
            <div className="space-y-4">
              {links.map((link) => (
                <div key={link.token} className="flex flex-col sm:flex-row sm:items-center justify-between p-4 border rounded-lg">
                  <div className="flex items-center gap-3 mb-2 sm:mb-0">
                    <Badge variant={link.active ? "default" : "secondary"}>
                      {link.active ? "Active" : "Inactive"}
                    </Badge>
                    <div>
                      <p className="font-medium break-all">{link.url}</p>
                      <p className="text-sm text-muted-foreground">Token: {link.token.slice(0, 10)}...</p>
                    </div>
                  </div>
                  <div className="flex gap-2">
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => actions.copyLink(link.url)}
                    >
                      <Copy className="h-4 w-4 mr-2" />
                      Copy
                    </Button>
                    {link.active && (
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => actions.deactivate(link.token)}
                      >
                        Deactivate
                      </Button>
                    )}
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => actions.rotate(link.token)}
                    >
                      Rotate
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
